.lightbox-foundation {
  position: fixed;
  margin: 0 auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.2);
  /*dim the background*/ }

.lightbox-background {
  position: absolute;
  margin: 0 auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.2);
  /*dim the background*/ }

.lightbox {
  font-family: sans-serif;
  z-index: 10;
  margin: auto;
  margin-top: 25px;
  padding: 25px;
  width: 400px;
  height: 70%;
  background-color: white;
  border-radius: 5px;
  border-color: #2665C4;
  border-style: solid;
  border-width: 3px; }

.close {
  float: right;
  padding: 10px;
  cursor: pointer; }
