#container {
  display: grid;
  grid-template-columns: 15% 15% auto 15% 15%;
  grid-template-rows: 10% 5% auto;
  padding: 10px; }

.grid-header {
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1; }

.nav {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
  background-color: #2665C4;
  border-radius: 5px;
  padding: 55px;
  cursor: pointer;
  margin-bottom: 20px; }

.content {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 3;
  background-color: white;
  border-radius: 5px;
  padding: 55px; }
  .content a, .content a:visited, .content a:hover, .content a:active {
    color: #2665C4;
    text-decoration: none; }
  .content a:hover, .content a:active {
    color: #03c203;
    text-decoration: none;
    color: #03c203; }

.request-box {
  grid-column-start: 5;
  grid-column-end: span col5-end;
  grid-row-start: 1;
  grid-row-end: row1-end; }

.headline {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 22px; }

.subheadline {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 15px; }

.text {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 12px; }

.list {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 15px; }

a, a:visited {
  font-family: sans-serif;
  text-decoration: none;
  color: white; }

a:active, a:hover {
  font-family: sans-serif;
  text-decoration: none;
  color: #03c203; }
